/* 달력 전체 스타일 */
.calendar {
  width: 100%;
  max-width: 600px; /* 최대 너비 설정 */
  margin: 20px auto;
  font-family: Arial, sans-serif;
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.calendar button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
  color: #333;
}

/* 달력 헤더 스타일 */
.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #f7f7f7;
  font-size: 18px;
  font-weight: bold;
  color: #333;
}

/* 달력 그리드 */
.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr); /* 7일을 한 줄에 배치 */
  grid-template-rows: repeat(5, 1fr); /* 한 달을 5주로 분할 */
  gap: 10px;
  padding: 10px;
}

/* 달력 날짜 스타일 */
.calendar-day {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  cursor: pointer;
  background-color: #fff;
  border-radius: 8px;
  transition: background-color 0.2s;
}

/* 비어있는 날짜 */
.calendar-day.empty {
  background-color: #f4f4f4;
}

/* 날짜 hover 스타일 */
.calendar-day:hover {
  background-color: #e2e2e2;
}

.calendar-day {
  position: relative;
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  border: 1px solid #ddd;
  cursor: pointer;
}

.calendar-day.has-event {
  background-color: #f0f8ff; /* 연한 파란색 */
}

.calendar-day.has-event::after {
  content: '';
  position: absolute;
  bottom: 4px;
  left: 50%;
  transform: translateX(-50%);
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #007bff; /* 파란색 점 표시 */
}


/* Modal Overlay (배경 흐리게 처리) */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* 반투명 배경 */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Modal Content (모달 박스) */
.modal-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  text-align: center;
  position: relative;
}

/* 모달 닫기 버튼 */
.modal-close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: transparent;
  border: none;
  font-size: 20px;
  cursor: pointer;
}

/* 달력 그리드 */
.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 10px;
  padding: 10px;
}

.calendar-day {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  padding: 10px;
  transition: background-color 0.2s ease;
}

.calendar-day:hover {
  background-color: #f0f0f0;
}

.empty {
  background-color: transparent;
}


/* 미디어 쿼리 - 모바일 대응 */
@media (max-width: 768px) {
  .calendar {
    max-width: 100%; /* 모바일에서는 최대 너비를 화면 크기에 맞춤 */
    margin: 10px; /* 여백 감소 */
  }

  .calendar-header {
    font-size: 16px; /* 모바일에서 글씨 크기 줄이기 */
    padding: 8px;
  }

  .calendar-header button {
    font-size: 14px; /* 모바일에서 글씨 크기 줄이기 */
    padding: 10px;
    width: 30px;
  }

  .calendar-grid {
    grid-template-columns: repeat(7, 1fr); /* 모바일에서는 동일하게 7일 표시 */
    grid-template-rows: repeat(6, 1fr); /* 6주를 표시할 수 있도록 설정 */
    gap: 8px;
  }

  .calendar-day {
    font-size: 14px; /* 작은 글씨 크기 */
    padding: 8px; /* 작은 패딩 */
  }

  /* 이벤트 표시 크기 조정 */
  .event-indicator {
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .calendar-header {
    font-size: 14px; /* 작은 화면에서 글씨 크기 더 줄이기 */
  }

  .calendar-grid {
    grid-template-columns: repeat(7, 1fr); /* 여전히 7일을 동일하게 표시 */
    gap: 6px;
  }

  .calendar-day {
    font-size: 12px; /* 더 작은 글씨 */
    padding: 6px;
  }

  .event-indicator {
    font-size: 10px; /* 더 작은 크기 */
  }
}
